import React, { useState, useEffect } from 'react';
import { Typography, Spin, message, Button, Modal, Alert } from 'antd';
import { useNavigate } from 'react-router-dom';
import axios from 'axios';
import './DynastyList.css';
import { dynastyData } from '../data/dynasties';

const { Title, Paragraph } = Typography;

function DynastyList() {
  const [dynasties, setDynasties] = useState([]);
  const [selectedDynasty, setSelectedDynasty] = useState(null);
  const [selectedKing, setSelectedKing] = useState(null);
  const [modalVisible, setModalVisible] = useState(false);
  const [kingWikiData, setKingWikiData] = useState(null);
  const [loadingWiki, setLoadingWiki] = useState(false);
  const [imagePreviewVisible, setImagePreviewVisible] = useState(false);
  const [previewImage, setPreviewImage] = useState('');

  useEffect(() => {
    // Chuyn i d liu t object sang array ngay trong frontend
    const dynastiesArray = Object.entries(dynastyData).map(([name, data]) => ({
      name,
      ...data
    }));
    setDynasties(dynastiesArray);
  }, []);

  const handleTimelineClick = (dynasty) => {
    setSelectedDynasty(dynasty.name);
  };

  const handleKingClick = async (king, dynastyName) => {
    try {
      setSelectedKing(king);
      setModalVisible(true);
      setLoadingWiki(true);
      
      console.log(`Tm thng tin cho vua: ${king.title || king.name}`);
      
      // Dng chnh xc tn v title ca vua  tm kim c th
      const searchStrategies = [
        // u tin tm theo title ca vua (thng l tn miu hiu) - y l cch tm chnh xc nht
        king.title ? `${king.title} vua` : null,
        // Tm theo tn v title y 
        king.title ? `${king.title} ${king.name}` : null,
        // Tm vi t kha vua + tn tht
        `${king.name} vua`,
        // Tm theo tn + t kha hong 
        `${king.name} hong `,
        // Tm theo nin hiu nu c
        king.reignName ? (Array.isArray(king.reignName) ? king.reignName[0] : king.reignName) : null,
        // Ch tm theo tn (t chnh xc nht)
        king.name
      ].filter(Boolean);
      
      let wikiData = null;
      
      // Th tng chin lc tm kim
      for (const searchQuery of searchStrategies) {
        try {
          console.log(`ang tm vi: "${searchQuery}"`);
          const results = await searchWikipedia(searchQuery);
          
          if (!results || results.length === 0) {
            console.log(`Khng c kt qu cho: "${searchQuery}"`);
            continue;
          }
          
          // Kim tra tng kt qu  tm thng tin chnh xc v v vua
          for (const result of results) {
            // Kim tra xem kt qu c phi l v v vua hay khng, khng phi v triu i
            const isAboutKing = isResultAboutKing(result, king, dynastyName);
            if (isAboutKing) {
              console.log(`Tm thy thng tin v vua: ${result.title}`);
              const fullContent = await fetchFullWikipediaContent(result.pageid);
              
              if (fullContent) {
                wikiData = fullContent;
                break;
              }
            } else {
              console.log(`Kt qu "${result.title}" khng phi v v vua, tip tc tm...`);
            }
          }
          
          if (wikiData) break;
        } catch (error) {
          console.error(`Li khi tm vi "${searchQuery}":`, error);
        }
      }
      
      if (wikiData) {
        setKingWikiData({
          ...king,
          wikiExtract: wikiData.extract,
          wikiImage: wikiData.image,
          wikiTitle: wikiData.title,
          dynasty: {
            name: dynastyName,
            period: dynastyData[dynastyName].period
          }
        });
      } else {
        // Fallback khi khng tm thy thng tin t Wikipedia
        console.log("Khng tm thy thng tin Wikipedia chnh xc, s dng d liu c sn");
        const formattedBiography = formatLocalBiography(king, dynastyName);
        
        setKingWikiData({
          ...king,
          wikiExtract: formattedBiography,
          dynasty: {
            name: dynastyName,
            period: dynastyData[dynastyName].period
          }
        });
      }
    } catch (error) {
      console.error("Li khi ly thng tin:", error);
      // D liu fallback
      const formattedBiography = formatLocalBiography(king, dynastyName);
      setKingWikiData({
        ...king,
        wikiExtract: formattedBiography,
        dynasty: {
          name: dynastyName,
          period: dynastyData[dynastyName].period
        }
      });
    } finally {
      setLoadingWiki(false);
    }
  };

  // Hm mi: Tm kim Wikipedia v tr v danh sch kt qu
  const searchWikipedia = async (query) => {
    try {
      const searchUrl = 'https://vi.wikipedia.org/w/api.php?' + 
        new URLSearchParams({
          action: 'query',
          list: 'search',
          srsearch: query,
          format: 'json',
          origin: '*',
          utf8: '1',
          srlimit: '10' // Ly 10 kt qu u tin  c nhiu la chn hn
        }).toString();
        
      const searchResponse = await axios.get(searchUrl);
      
      if (!searchResponse.data?.query?.search || searchResponse.data.query.search.length === 0) {
        return null;
      }
      
      return searchResponse.data.query.search;
    } catch (error) {
      console.error("Li tm kim Wikipedia:", error);
      return null;
    }
  };

  // Hm mi: Kim tra xem kt qu c phi v v vua c th hay khng
  const isResultAboutKing = (result, king, dynastyName) => {
    const title = result.title.toLowerCase();
    const snippet = result.snippet.toLowerCase();
    const kingName = king.name.toLowerCase();
    const kingTitle = king.title ? king.title.toLowerCase() : '';
    
    // Loi b cc kt qu v triu i
    if (title.includes("nh") && title.includes(dynastyName.toLowerCase().replace("nh ", ""))) {
      return false;
    }
    
    if (title.includes("triu") && title.includes(dynastyName.toLowerCase().replace("nh ", ""))) {
      return false;
    }
    
    // Kim tra xem tiu  c cha tn hoc title ca vua khng
    const hasTitleOrName = (kingTitle && title.includes(kingTitle)) || title.includes(kingName);
    
    // Kim tra xem ni dung tm tt c cha t kha v vua khng
    const hasKingKeywords = snippet.includes("vua") || 
                             snippet.includes("hong ") || 
                             snippet.includes("tr v") ||
                             snippet.includes("ln ngi");
    
    // Kim tra xem ni dung c  cp n nin hiu khng
    const hasReignInfo = king.period && snippet.includes(king.period);
    
    // im s nh gi mc  ph hp
    let relevanceScore = 0;
    
    if (hasTitleOrName) relevanceScore += 3;
    if (hasKingKeywords) relevanceScore += 2;
    if (hasReignInfo) relevanceScore += 2;
    
    // Kim tra nu c nin hiu
    if (king.reignName) {
      const reignNames = Array.isArray(king.reignName) ? king.reignName : [king.reignName];
      for (const reign of reignNames) {
        if (snippet.includes(reign.toLowerCase())) {
          relevanceScore += 3;
          break;
        }
      }
    }
    
    return relevanceScore >= 3; // Ngng im  cao  kt lun y l thng tin v vua
  };

  // Hm mi: Ly ni dung y  ca mt trang Wikipedia
  const fetchFullWikipediaContent = async (pageId) => {
    try {
      const contentUrl = 'https://vi.wikipedia.org/w/api.php?' + 
        new URLSearchParams({
          action: 'query',
          pageids: pageId,
          prop: 'extracts|pageimages|categories|images',
          format: 'json',
          origin: '*',
          explaintext: '1',
          exsectionformat: 'wiki',
          piprop: 'original',
          imlimit: 'max',
          formatversion: '2'
        }).toString();
        
      const contentResponse = await axios.get(contentUrl);
      const page = contentResponse.data.query.pages[0];
      
      if (!page || !page.extract) {
        return null;
      }
      
      // Loi b cc phn khng cn thit
      let content = page.extract;
      const sectionsToRemove = [
        '== Ch thch ==', '== Ghi ch ==', '== Tham kho ==', 
        '== Xem thm ==', '== Lin kt ngoi =='
      ];
      
      sectionsToRemove.forEach(section => {
        const sectionIndex = content.indexOf(section);
        if (sectionIndex !== -1) {
          content = content.substring(0, sectionIndex);
        }
      });
      
      // Tm hnh nh u tin v tng
      let imageUrl = null;
      const statueKeywords = ['tng', 'statue', 'monument', 'i'];
      
      if (page.original?.source) {
        imageUrl = page.original.source;
      } else if (page.images && page.images.length > 0) {
        // Lc v tm hnh nh tng
        const possibleStatueImages = page.images.filter(img => {
          const imgTitle = img.title.toLowerCase();
          return statueKeywords.some(keyword => imgTitle.includes(keyword)) &&
                 (imgTitle.endsWith('.jpg') || imgTitle.endsWith('.png') || imgTitle.endsWith('.jpeg'));
        });
        
        if (possibleStatueImages.length > 0) {
          try {
            // Ly URL ca hnh nh tng u tin
            const imageInfoUrl = 'https://vi.wikipedia.org/w/api.php?' + 
              new URLSearchParams({
                action: 'query',
                prop: 'imageinfo',
                iiprop: 'url',
                titles: possibleStatueImages[0].title,
                format: 'json',
                origin: '*',
                formatversion: '2'
              }).toString();
              
            const imageResponse = await axios.get(imageInfoUrl);
            const statueUrl = imageResponse.data.query.pages[0]?.imageinfo?.[0]?.url;
            
            if (statueUrl) {
              imageUrl = statueUrl;
            }
          } catch (error) {
            console.error("Li khi ly hnh nh tng:", error);
          }
        }
      }
      
      return {
        title: page.title,
        extract: content,
        image: imageUrl,
        categories: page.categories?.map(c => c.title) || []
      };
    } catch (error) {
      console.error("Li khi ly ni dung y :", error);
      return null;
    }
  };

  // nh dng tiu s t d liu local khi khng c Wikipedia
  const formatLocalBiography = (king, dynastyName) => {
    const sections = [];
    
    // Phn gii thiu
    sections.push(`${king.title || king.name} (${king.period}) l v vua ca ${dynastyName}.`);
    
    if (king.description) {
      sections.push(king.description);
    }
    
    // Phn tiu s chi tit
    if (king.biography) {
      sections.push("== Tiu s ==");
      sections.push(king.biography);
    }
    
    // Phn thnh tu
    if (king.achievements && king.achievements.length > 0) {
      sections.push("== Thnh tu ni bt ==");
      const achievements = king.achievements.map(item => ` ${item}`).join('\n');
      sections.push(achievements);
    }
    
    // Phn s kin
    if (king.events && king.events.length > 0) {
      sections.push("== Cc s kin chnh ==");
      const events = king.events.map(item => ` ${item}`).join('\n');
      sections.push(events);
    }
    
    // Phn nin hiu
    if (king.reignName) {
      sections.push("== Nin hiu ==");
      const reignNames = Array.isArray(king.reignName) 
        ? king.reignName.join('\n') 
        : king.reignName;
      sections.push(reignNames);
    }
    
    return sections.join('\n\n');
  };

  // Ci tin hm x l ni dung  loi b cc phn khng cn thit
  const formatWikipediaContent = (content) => {
    if (!content) return null;
    
    const sections = [];
    let currentSection = { title: null, content: [] };
    let inList = false;
    let listItems = [];
    
    // Loi b cc phn khng mong mun (phn tch tng dng)
    const lines = content.split('\n');
    let skipSection = false;
    
    lines.forEach((line, index) => {
      // Kim tra nu ang  phn cn b qua
      if (line.startsWith('==') && line.endsWith('==')) {
        const sectionTitle = line.replace(/=/g, '').trim().toLowerCase();
        
        // Kim tra nu section hin ti l mt phn cn b qua
        const unwantedSections = ['ch thch', 'ghi ch', 'tham kho', 'xem thm', 
          'lin kt ngoi', 'ti liu', 'references', 'notes', 'further reading', 'external links'];
        
        if (unwantedSections.some(s => sectionTitle.includes(s))) {
          skipSection = true;
          
          // Lu section hin ti nu c d liu
          if (currentSection.content.length > 0 || currentSection.title) {
            if (inList) {
              currentSection.content.push({ type: 'list', items: [...listItems] });
              listItems = [];
              inList = false;
            }
            sections.push({...currentSection});
          }
          
          currentSection = { title: null, content: [] };
          return;
        }
        
        skipSection = false;
        
        // X l tiu  section bnh thng
        if (currentSection.content.length > 0 || currentSection.title) {
          if (inList) {
            currentSection.content.push({ type: 'list', items: [...listItems] });
            listItems = [];
            inList = false;
          }
          sections.push({...currentSection});
        }
        
        const level = (line.match(/=/g) || []).length / 2;
        currentSection = {
          title: line.replace(/=/g, '').trim(),
          level,
          content: []
        };
        return;
      }
      
      // B qua ni dung trong cc section khng mong mun
      if (skipSection) return;
      
      // X l ni dung bnh thng
      if (!line.trim()) {
        if (inList && listItems.length > 0) {
          currentSection.content.push({ type: 'list', items: [...listItems] });
          listItems = [];
          inList = false;
        }
        return;
      }
      
      // X l danh sch
      if (line.startsWith('*') || line.startsWith('-') || line.startsWith('') || /^\d+\./.test(line)) {
        if (!inList) inList = true;
        listItems.push(line.replace(/^[*-]\s*|\d+\.\s*/, '').trim());
        return;
      }
      
      if (inList) {
        currentSection.content.push({ type: 'list', items: [...listItems] });
        listItems = [];
        inList = false;
      }
      
      // on vn thng thng
      currentSection.content.push({ type: 'paragraph', text: line.trim() });
    });
    
    // Thm section cui cng nu khng phi section cn b qua
    if (!skipSection && (currentSection.content.length > 0 || currentSection.title)) {
      if (inList) {
        currentSection.content.push({ type: 'list', items: [...listItems] });
      }
      sections.push(currentSection);
    }
    
    // Render sections nh trc
    return (
      <div className="wiki-full-content">
        {sections.map((section, sIndex) => (
          <div key={`section-${sIndex}`} className="wiki-section">
            {section.title && (
              <Title 
                level={Math.min(section.level + 3, 5)} 
                className="wiki-section-title"
                id={`section-${section.title.replace(/\s+/g, '-').toLowerCase()}`}
              >
                {section.title}
              </Title>
            )}
            
            {section.content.map((item, cIndex) => {
              if (item.type === 'paragraph') {
                return <Paragraph key={`p-${sIndex}-${cIndex}`}>{item.text}</Paragraph>;
              } else if (item.type === 'list') {
                return (
                  <ul key={`list-${sIndex}-${cIndex}`} className="wiki-list">
                    {item.items.map((li, lIndex) => (
                      <li key={`li-${sIndex}-${cIndex}-${lIndex}`}>{li}</li>
                    ))}
                  </ul>
                );
              }
              return null;
            })}
          </div>
        ))}
      </div>
    );
  };

  const KingDetailModal = ({ king, visible, onClose, loading, wikiData }) => {
    if (!king) return null;

    return (
      <Modal
        title={
          <div className="king-modal-title">
            <Title level={4}>{king.title || king.name}</Title>
            <span className="king-period">Thi gian tr v: {king.period}</span>
            {wikiData?.wikiTitle && (
              <div className="wiki-source">
                Ngun: <a href={`https://vi.wikipedia.org/wiki/${encodeURIComponent(wikiData.wikiTitle)}`} 
                        target="_blank" rel="noopener noreferrer">Wikipedia</a>
              </div>
            )}
          </div>
        }
        open={visible}
        onCancel={onClose}
        footer={null}
        width={800}
        className="king-modal"
      >
        <div className="king-modal-content">
          {loading ? (
            <div className="loading-container">
              <Spin size="large" />
              <p>ang ti thng tin t Wikipedia...</p>
            </div>
          ) : (
            <>
              <div className="king-info-container">
                {wikiData?.wikiImage && wikiData.wikiImage !== '' && (
                  <div className="king-image-container">
                    <img
                      src={wikiData.wikiImage}
                      alt={king.name}
                      className="king-portrait"
                      onClick={() => {
                        setPreviewImage(wikiData.wikiImage);
                        setImagePreviewVisible(true);
                      }}
                      onError={(e) => {
                        e.target.style.display = 'none';
                      }}
                    />
                  </div>
                )}

                <div className="king-basic-info">
                  {king.reignName && (
                    <Paragraph>
                      <strong>Nin hiu:</strong>{' '}
                      {Array.isArray(king.reignName) 
                        ? king.reignName.join(', ') 
                        : king.reignName}
                    </Paragraph>
                  )}
                  
                  {king.description && (
                    <Paragraph>
                      <strong>Tm tt:</strong> {king.description}
                    </Paragraph>
                  )}
                </div>
              </div>

              {wikiData?.wikiExtract && (
                <div className="king-wiki-info">
                  {wikiData.wikiTitle && (
                    <div className="wiki-header">
                      <Title level={4} className="wiki-title">{wikiData.wikiTitle}</Title>
                    </div>
                  )}
                  {formatWikipediaContent(wikiData.wikiExtract)}
                  {wikiData.wikiTitle && (
                    <div className="wiki-footer">
                      <a 
                        href={`https://vi.wikipedia.org/wiki/${encodeURIComponent(wikiData.wikiTitle)}`} 
                        target="_blank" 
                        rel="noopener noreferrer"
                        className="wiki-source-link"
                      >
                        Xem y  trn Wikipedia
                      </a>
                    </div>
                  )}
                </div>
              )}

              {king.achievements && !wikiData?.wikiExtract?.includes("Thnh tu") && (
                <div className="king-achievements-section">
                  <Title level={5}>Thnh tu ni bt</Title>
                  <ul>
                    {king.achievements.map((achievement, index) => (
                      <li key={index}>{achievement}</li>
                    ))}
                  </ul>
                </div>
              )}

              {king.events && !wikiData?.wikiExtract?.includes("S kin") && (
                <div className="king-events-section">
                  <Title level={5}>Cc s kin quan trng</Title>
                  <ul>
                    {king.events.map((event, index) => (
                      <li key={index}>{event}</li>
                    ))}
                  </ul>
                </div>
              )}
            </>
          )}
        </div>
      </Modal>
    );
  };

  return (
    <>
      <Title className="page-title" level={2}>
        Cc Triu i v Vua Cha Vit Nam
      </Title>

      <div className="dynasty-container">
        <div className="timeline-section">
          <div className="timeline-container">
            {dynasties.map((dynasty) => (
              <div 
                key={dynasty.name} 
                className="timeline-item"
                onClick={() => handleTimelineClick(dynasty)}
              >
                <div className={`timeline-content ${selectedDynasty === dynasty.name ? 'active' : ''}`}>
                  <div className="timeline-title">{dynasty.name}</div>
                  <div className="timeline-period">{dynasty.period}</div>
                </div>
              </div>
            ))}
          </div>
        </div>

        <div className="detail-section">
          {selectedDynasty ? (
            <div className="dynasty-detail">
              {dynasties
                .filter(d => d.name === selectedDynasty)
                .map(dynasty => (
                  <div key={dynasty.name}>
                    <Title level={3}>
                      {dynasty.name} ({dynasty.period})
                    </Title>

                    <div className="dynasty-info">
                      <div className="dynasty-main-info">
                        <Title level={4}>Thng tin chnh</Title>
                        {dynasty.capital && (
                          <Paragraph>
                            <strong>Kinh :</strong> {dynasty.capital}
                          </Paragraph>
                        )}
                        <Paragraph>{dynasty.description}</Paragraph>
                      </div>

                      <div className="dynasty-additional-info">
                        <Title level={4}>Thng tin thm</Title>
                        {dynasty.achievements && (
                          <div>
                            <strong>Thnh tu ni bt:</strong>
                            <ul>
                              {dynasty.achievements.map((achievement, index) => (
                                <li key={index}>{achievement}</li>
                              ))}
                            </ul>
                          </div>
                        )}
                        {dynasty.events && (
                          <div>
                            <strong>S kin quan trng:</strong>
                            <ul>
                              {dynasty.events.map((event, index) => (
                                <li key={index}>{event}</li>
                              ))}
                            </ul>
                          </div>
                        )}
                      </div>
                    </div>

                    <Title level={4}>Danh sch cc v vua</Title>
                    <div className="kings-list">
                      {dynasty.kings.map(king => (
                        <div 
                          key={king.id} 
                          className="king-item"
                          onClick={() => handleKingClick(king, dynasty.name)}
                        >
                          <div className="king-title">
                            {king.title || king.name}
                          </div>
                          <div className="king-info">
                            <div>{king.description}</div>
                            {king.reignName && (
                              <div className="king-reign-name">
                                Nin hiu: {Array.isArray(king.reignName) 
                                  ? king.reignName.join(', ') 
                                  : king.reignName}
                              </div>
                            )}
                          </div>
                        </div>
                      ))}
                    </div>
                  </div>
                ))}
            </div>
          ) : (
            <div className="dynasty-detail" style={{ textAlign: 'center' }}>
              <Title level={3}>Chn mt triu i  xem chi tit</Title>
            </div>
          )}
        </div>
      </div>

      <KingDetailModal
        king={selectedKing}
        visible={modalVisible}
        onClose={() => setModalVisible(false)}
        loading={loadingWiki}
        wikiData={kingWikiData}
      />

      <Modal
        visible={imagePreviewVisible}
        footer={null}
        onCancel={() => setImagePreviewVisible(false)}
        closable={true}
        className="image-preview-modal"
        centered
      >
        <img alt="Preview" src={previewImage} />
      </Modal>
    </>
  );
}

export default DynastyList;