import React, { useState } from 'react';
import { Input, Card } from 'antd';
import axios from 'axios';
import { Link } from 'react-router-dom';

const { Search } = Input;
const API_URL = process.env.REACT_APP_API_URL;

function SearchComponent() {
  const [results, setResults] = useState([]);
  const [loading, setLoading] = useState(false);

  const onSearch = async (value) => {
    if (!value.trim()) return;
    
    setLoading(true);
    try {
      const response = await axios.get(`${API_URL}/events/search?q=${value}`);
      setResults(response.data);
    } catch (error) {
      console.error('Error searching:', error);
    } finally {
      setLoading(false);
    }
  };

  return (
    <div style={{ padding: '20px' }}>
      <Search
        placeholder="Tm kim s kin lch s..."
        onSearch={onSearch}
        enterButton
        loading={loading}
        style={{ maxWidth: 500, margin: '20px auto' }}
      />
      <div className="search-results">
        {results.map((result) => (
          <Card key={result.id} style={{ marginBottom: 16 }}>
            <Link to={`/event/${result.id}`}>
              <h3>{result.title}</h3>
            </Link>
            <p>{result.description?.substring(0, 200)}...</p>
            <p>Thi gian: {result.date}</p>
          </Card>
        ))}
      </div>
    </div>
  );
}

export default SearchComponent; 